// ===================================
// FRONTEND - Next.js Contact Page
// ===================================

"use client";
import React, { useState } from "react";
import {
  Mail,
  Phone,
  MapPin,
  Clock,
  Send,
  CheckCircle,
  Printer,
  Globe,
  Package,
} from "lucide-react";
import HeroSection from "./HeroSection";
import ContactForm from ""
const ContactPage = () => {
  const [formData, setFormData] = useState({
    first_name: "",
    last_name: "",
    email: "",
    phone: "",
    company: "",
    service_type: "",
    message: "",
  });

  const [loading, setLoading] = useState(false);
  const [success, setSuccess] = useState(false);
  const [error, setError] = useState("");

  const serviceTypes = [
    "Offset Printing",
    "Digital Printing",
    "Book Printing",
    "Business Cards & Stationery",
    "Marketing Materials",
    "Promotional Products",
    "Graphic Design",
    "Web Design & Development",
    "Packaging & Labels",
    "Other",
  ];

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    setLoading(true);
    setError("");
    setSuccess(false);

    try {
      const response = await fetch("http://localhost:8000/api/contact/", {
        method: "POST",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(formData),
      });

      const data = await response.json();

      if (response.ok) {
        setSuccess(true);
        setFormData({
          first_name: "",
          last_name: "",
          email: "",
          phone: "",
          company: "",
          service_type: "",
          message: "",
        });

        // Reset success message after 5 seconds
        setTimeout(() => setSuccess(false), 5000);
      } else {
        setError(data.error || "Something went wrong. Please try again.");
      }
    } catch (err) {
      setError(
        "Failed to submit form. Please check your connection and try again."
      );
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="bg-white">
      {/* Hero Section - 1920x1080 */}
      <HeroSection />

      {/* Contact Information Section */}
      <ContactInformationSection />

      {/* Contact Form Section */}
      <section className="py-20 px-4 bg-white">
        <div className="max-w-7xl mx-auto">
          <div className="grid lg:grid-cols-2 gap-12">
            {/* Left Column - Form */}
            <div>
              <div className="mb-8">
                <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
                  Get in Touch
                </h2>
                <p className="text-xl text-gray-600">
                  Fill out the form below and we'll get back to you within 24
                  hours
                </p>
              </div>

              <form onSubmit={handleSubmit} className="space-y-6">
                {/* Name Fields */}
                <div className="grid md:grid-cols-2 gap-6">
                  <div>
                    <label
                      htmlFor="first_name"
                      className="block text-sm font-semibold text-gray-900 mb-2"
                    >
                      First Name *
                    </label>
                    <input
                      type="text"
                      id="first_name"
                      name="first_name"
                      value={formData.first_name}
                      onChange={handleChange}
                      required
                      className="w-full px-4 py-3 bg-gray-50 border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:bg-white focus:outline-none transition-all"
                      placeholder="John"
                    />
                  </div>
                  <div>
                    <label
                      htmlFor="last_name"
                      className="block text-sm font-semibold text-gray-900 mb-2"
                    >
                      Last Name *
                    </label>
                    <input
                      type="text"
                      id="last_name"
                      name="last_name"
                      value={formData.last_name}
                      onChange={handleChange}
                      required
                      className="w-full px-4 py-3 bg-gray-50 border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:bg-white focus:outline-none transition-all"
                      placeholder="Doe"
                    />
                  </div>
                </div>

                {/* Email & Phone */}
                <div className="grid md:grid-cols-2 gap-6">
                  <div>
                    <label
                      htmlFor="email"
                      className="block text-sm font-semibold text-gray-900 mb-2"
                    >
                      Email Address *
                    </label>
                    <input
                      type="email"
                      id="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      required
                      className="w-full px-4 py-3 bg-gray-50 border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:bg-white focus:outline-none transition-all"
                      placeholder="john@example.com"
                    />
                  </div>
                  <div>
                    <label
                      htmlFor="phone"
                      className="block text-sm font-semibold text-gray-900 mb-2"
                    >
                      Phone Number
                    </label>
                    <input
                      type="tel"
                      id="phone"
                      name="phone"
                      value={formData.phone}
                      onChange={handleChange}
                      className="w-full px-4 py-3 bg-gray-50 border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:bg-white focus:outline-none transition-all"
                      placeholder="+93 XXX XXX XXX"
                    />
                  </div>
                </div>

                {/* Company */}
                <div>
                  <label
                    htmlFor="company"
                    className="block text-sm font-semibold text-gray-900 mb-2"
                  >
                    Company Name
                  </label>
                  <input
                    type="text"
                    id="company"
                    name="company"
                    value={formData.company}
                    onChange={handleChange}
                    className="w-full px-4 py-3 bg-gray-50 border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:bg-white focus:outline-none transition-all"
                    placeholder="Your Company"
                  />
                </div>

                {/* Service Type */}
                <div>
                  <label
                    htmlFor="service_type"
                    className="block text-sm font-semibold text-gray-900 mb-2"
                  >
                    Service Interested In *
                  </label>
                  <select
                    id="service_type"
                    name="service_type"
                    value={formData.service_type}
                    onChange={handleChange}
                    required
                    className="w-full px-4 py-3 bg-gray-50 border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:bg-white focus:outline-none transition-all"
                  >
                    <option value="">Select a service</option>
                    {serviceTypes.map((service, index) => (
                      <option key={index} value={service}>
                        {service}
                      </option>
                    ))}
                  </select>
                </div>

                {/* Message */}
                <div>
                  <label
                    htmlFor="message"
                    className="block text-sm font-semibold text-gray-900 mb-2"
                  >
                    Your Message *
                  </label>
                  <textarea
                    id="message"
                    name="message"
                    value={formData.message}
                    onChange={handleChange}
                    required
                    rows="5"
                    className="w-full px-4 py-3 bg-gray-50 border-2 border-gray-200 rounded-xl focus:border-blue-500 focus:bg-white focus:outline-none transition-all resize-none"
                    placeholder="Tell us about your project requirements..."
                  ></textarea>
                </div>

                {/* Success Message */}
                {success && (
                  <div className="bg-green-50 border-2 border-green-500 rounded-xl p-4 flex items-center gap-3">
                    <CheckCircle className="w-6 h-6 text-green-600 flex-shrink-0" />
                    <p className="text-green-800 font-medium">
                      Thank you! Your message has been sent successfully. We'll
                      get back to you soon.
                    </p>
                  </div>
                )}

                {/* Error Message */}
                {error && (
                  <div className="bg-red-50 border-2 border-red-500 rounded-xl p-4">
                    <p className="text-red-800">{error}</p>
                  </div>
                )}

                {/* Submit Button */}
                <button
                  type="submit"
                  disabled={loading}
                  className="w-full bg-blue-600 hover:bg-blue-700 disabled:bg-gray-400 text-white font-semibold py-4 px-6 rounded-xl transition-all duration-300 flex items-center justify-center gap-2 shadow-lg hover:shadow-xl"
                >
                  {loading ? (
                    <>
                      <div className="w-5 h-5 border-2 border-white border-t-transparent rounded-full animate-spin"></div>
                      Sending...
                    </>
                  ) : (
                    <>
                      <Send className="w-5 h-5" />
                      Send Message
                    </>
                  )}
                </button>
              </form>
            </div>

            {/* Right Column - Additional Info */}
            <div className="lg:pl-12">
              <div className="sticky top-24">
                <div className="bg-gradient-to-br from-blue-50 to-indigo-50 rounded-2xl p-8">
                  <h3 className="text-2xl font-bold text-gray-900 mb-6">
                    Why Choose Us?
                  </h3>
                  <div className="space-y-4">
                    {[
                      {
                        icon: "âš¡",
                        title: "Quick Turnaround",
                        desc: "Fast delivery without compromising quality",
                      },
                      {
                        icon: "ðŸŽ¨",
                        title: "Expert Design Team",
                        desc: "Professional designers with 20+ years experience",
                      },
                      {
                        icon: "âœ“",
                        title: "Quality Guaranteed",
                        desc: "Rigorous quality control on every project",
                      },
                      {
                        icon: "ðŸ’°",
                        title: "Competitive Pricing",
                        desc: "Best prices in Afghanistan",
                      },
                    ].map((item, index) => (
                      <div
                        key={index}
                        className="flex gap-4 bg-white rounded-xl p-4"
                      >
                        <div className="text-3xl">{item.icon}</div>
                        <div>
                          <h4 className="font-bold text-gray-900 mb-1">
                            {item.title}
                          </h4>
                          <p className="text-sm text-gray-600">{item.desc}</p>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </div>
  );
};



// Contact Information Section
const ContactInformationSection = () => {
  return (
    <section className="py-20 px-4 bg-gray-50">
      <div className="max-w-7xl mx-auto">
        <div className="text-center mb-16">
          <h2 className="text-4xl md:text-5xl font-bold text-gray-900 mb-4">
            Contact Information
          </h2>
          <p className="text-xl text-gray-600 max-w-3xl mx-auto">
            Reach out to us through any of these channels. We're here to help
            with all your printing needs.
          </p>
        </div>

        <div className="grid md:grid-cols-2 lg:grid-cols-4 gap-8">
          <ContactInfoCard
            icon={<MapPin className="w-6 h-6" />}
            title="Our Location"
            content="Kabul, Afghanistan"
            subContent="Serving all of Afghanistan"
            gradient="from-blue-500 to-blue-600"
          />

          <ContactInfoCard
            icon={<Phone className="w-6 h-6" />}
            title="Phone"
            content="+93 XXX XXX XXX"
            subContent="Mon-Sat: 8:00 AM - 6:00 PM"
            gradient="from-green-500 to-green-600"
          />

          <ContactInfoCard
            icon={<Mail className="w-6 h-6" />}
            title="Email"
            content="info@strengthsinnovation.com"
            subContent="We reply within 24 hours"
            gradient="from-purple-500 to-purple-600"
          />

          <ContactInfoCard
            icon={<Clock className="w-6 h-6" />}
            title="Business Hours"
            content="Monday - Saturday"
            subContent="8:00 AM - 6:00 PM"
            gradient="from-orange-500 to-orange-600"
          />
        </div>

        {/* Services Grid */}
        <div className="mt-16 bg-white rounded-2xl p-8 md:p-12 shadow-lg">
          <h3 className="text-3xl font-bold text-gray-900 mb-8 text-center">
            Our Services
          </h3>
          <div className="grid sm:grid-cols-2 lg:grid-cols-3 gap-6">
            {[
              {
                icon: <Printer className="w-6 h-6" />,
                text: "Commercial Printing",
                color: "blue",
              },
              {
                icon: <Package className="w-6 h-6" />,
                text: "Packaging Solutions",
                color: "green",
              },
              {
                icon: <Globe className="w-6 h-6" />,
                text: "Web Development",
                color: "purple",
              },
              { icon: "ðŸ“š", text: "Book Printing", color: "orange" },
              { icon: "ðŸŽ¨", text: "Graphic Design", color: "pink" },
              { icon: "ðŸŽ", text: "Promotional Products", color: "indigo" },
            ].map((item, index) => (
              <div
                key={index}
                className="flex items-center gap-4 bg-gray-50 rounded-xl p-4 hover:bg-gray-100 transition-all"
              >
                <div
                  className={`w-12 h-12 bg-${item.color}-600 rounded-lg flex items-center justify-center text-white text-xl`}
                >
                  {typeof item.icon === "string" ? item.icon : item.icon}
                </div>
                <span className="font-semibold text-gray-900">{item.text}</span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </section>
  );
};

// Contact Info Card Component
const ContactInfoCard = ({ icon, title, content, subContent, gradient }) => {
  return (
    <div className="bg-white rounded-2xl p-8 shadow-lg hover:shadow-2xl transition-all duration-300 group">
      <div
        className={`w-16 h-16 bg-gradient-to-br ${gradient} rounded-2xl flex items-center justify-center text-white mb-6 group-hover:scale-110 transition-transform`}
      >
        {icon}
      </div>
      <h4 className="font-bold text-gray-900 mb-2 text-lg">{title}</h4>
      <p className="text-gray-700 font-semibold mb-1">{content}</p>
      <p className="text-sm text-gray-600">{subContent}</p>
    </div>
  );
};

export default ContactPage;
